import React from 'react';
import Link from 'gatsby-link';
import { object, arrayOf, shape, string } from 'prop-types';

import { Button } from '../../ui';
import './Navigation.scss';

const key = (node) => `key-nav-item-${node.fields.slug}`;
const sortPages = (a, b) => {
    if (a.node.fields.slug < b.node.fields.slug) {
        return -1;
    } else if (a.node.fields.slug > b.node.fields.slug) {
        return 1;
    }
    return 0;
};

class Navigation extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isTop: true,
            intervalId: 0
        };

        this.onScroll = this.onScroll.bind(this);
        this.scrollToTop = this.scrollToTop.bind(this);
        this.scrollStep = this.scrollStep.bind(this);
    }

    componentDidMount() {
        document.addEventListener('scroll', this.onScroll, false);
    }

    componentWillUnmount() {
        document.removeEventListener('scroll', this.onScroll, false);
    }

    onScroll() {
        this.setState({ isTop: window.scrollY < 100 })
    }

    scrollStep() {
        if (window.pageYOffset === 0) {
            clearInterval(this.state.intervalId);
        }

        const step = 10;

        window.scroll(0, window.pageYOffset - step);
    }
  
    scrollToTop() {
        const intervalId = setInterval(this.scrollStep, this.props.delayInMs);
        this.setState({ intervalId: intervalId });
    }

    render() {
        const { pages } = this.props;
        return (
            <ul className="nav__container">
                {
                    pages.sort(sortPages).map(({ node }) => (
                        <li key={key(node)}>
                            <Link to={node.fields.slug}
                                className="button-text-small">
                                {node.frontmatter.title}
                            </Link>
                        </li>
                    ))
                }
                <li>
                    <Button
                        small
                        outline={this.state.isTop}
                        onClick={this.scrollToTop}>
                        {'Free Trial Now'}
                    </Button>
                </li>
            </ul>
        );
    }
}

Navigation.propTypes = {
    pages: arrayOf(shape({
        node: shape({
            fields: shape({
                slug: string
            })
        })
    }))
};

export default Navigation;



