import React from 'react';
import { object, arrayOf, shape} from 'prop-types';
import Link from 'gatsby-link';

import './Footer.scss';
import { SalesheroLogo } from '../../components/CustomSVG/Svg';

const getNavItems = (items) =>
	items.filter(i => {
		const scope = i.node.frontmatter.scope;
		return scope && scope.indexOf('FOOTER') > -1
	});

const Footer = ({ data }) =>
	<section className="footer__container">

		<div className="footer__top">
			<div className="footer__nav">
				<Link to="/"
					className="logo__container">
					<SalesheroLogo height="18" />
				</Link>

				<div className="nav__items">
					{
						getNavItems(data.allMarkdownRemark.edges)
						.map((item, index) => (
							<Link to={item.node.fields.slug}
								key={index}
								className="button-text-small">
								{item.node.frontmatter.title}
							</Link>
						))
					}
				</div>
			</div>

			<p className="small-label">
				At SalesHero, our mission is to empower sales teams through artificial intelligence. We are located in San Francisco, CA.
			</p>
		</div>

		<div className="underline">
		</div>

		<div className="footer__bottom">
			<div className="footer__tm">
				&#169; 2017 SalesHero, Inc.
			</div>

			{/* <div className="footer__terms">
				<Link to="/terms">
					Terms of Service
				</Link>
				<Link to="/privacy">
					Privacy policy
				</Link>
			</div> */}
		</div>

	</section>

Footer.propTypes = {
	data: shape({
		allMarkDownRemark: shape({
			edges: arrayOf(object)
		})
	})
}

export default Footer;



