import React from 'react';
import { shape, object, arrayOf } from 'prop-types';
import Link from 'gatsby-link';

import { SalesheroLogo } from '../../components/CustomSVG/Svg';
import Navigation from '../../components/Navigation/Navigation';

import './Header.scss';

const getNavItems = (items) =>
	items.filter(i => {
		const scope = i.node.frontmatter.scope;
		return scope && scope.indexOf('HEADER') > -1
	});

const Header = ({ data }) =>
	<div className="header__container">
		<a href="http://saleshero.ai"
			className="logo__container">
			<SalesheroLogo height="18" />
		</a>

		<Navigation pages={getNavItems(data.allMarkdownRemark.edges)} />
	</div>

Header.propTypes = {
	data: shape({
		allMarkDownRemark: shape({
			edges: arrayOf(object)
		})
	})
}

export default Header


